import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { EurekaArrowUp } from '@Green-Dot-Corporation/eureka-ui-icons';
import WindowContainer from '../window-container/WindowContainer';
import ChatTextBubble from './ChatTextBubble';
import ChatImageBubble from './ChatImageBubble';
import MessageInput from '../message-input/MessageInput';
import Attach from '../svg/Attach';
// Styles
import './styles/chat-window.scss';

const { PRIMARY, SECONDARY } = VARIANTS;
class ChatWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        i18n: PropTypes.object,
    };

    state = {
        shouldShowCounter: false,
        count: 0,
        isSendMessageBtnEnabled: false,
    };

    render() {
        const { contextCls, i18n, ...restProps } = this.props;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                <WindowContainer
                    contextCls={cx(`${this.baseCls}__chat-window-container`)}
                    title="Chat with an agent" // {i18n.getTranslation('chatWindow.title')}
                    shouldShowCloseBtn
                    shouldShowMinimizeBtn
                    onCloseBtnClick={this.handleCloseBtnClick}
                    onMinimizeBtnClick={this.handleMinimizeBtnClick}
                    i18n={i18n}
                >
                    <div className={`${this.baseCls}__chat-text-container`}>
                        <div className={`${this.baseCls}__messages-container`}>
                            {/* TODO: Replace the below to really chat text when API is ready */}
                            <ChatTextBubble text="I’m having an issue with my direct deposit not showing up in my account. Can you help?" />
                            <ChatTextBubble
                                text="Let me look that up for you. Hold one moment while I access your account."
                                isTextFromMe={false}
                            />
                            <ChatImageBubble
                                fileName="IMG_2474.jpeg"
                                isLoading
                                {...restProps}
                            />

                            <ChatImageBubble
                                imageSource="https://www.sbtpg.com/wp-content/uploads/2021/11/413_01127.jpg"
                                fileName="IMG_2474.jpeg"
                                i18n={i18n}
                                {...restProps}
                            />
                            <ChatImageBubble
                                fileName="image.png"
                                imageSource="https://www.google.com/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png"
                                i18n={i18n}
                                {...restProps}
                            />
                        </div>
                        <div className="send-message-container">
                            <MessageInput
                                contextCls={`${this.baseCls}__message-input`}
                                placeholder={i18n.getTranslation(
                                    'chatWindow.messageInputPlaceholder',
                                )}
                                aria-label={i18n.getTranslation(
                                    'chatWindow.messageInputAriaLabel',
                                )}
                                onResize={this.handleResize}
                                onChange={this.handleMessageChange}
                                renderAfterControl={this.renderAfter}
                            />
                        </div>
                    </div>
                </WindowContainer>
            </div>
        );
    }

    renderCounter = () => {
        return (
            <div className={`${this.baseCls}__counter-container`}>
                <span>{this.state.count}/280</span>
            </div>
        );
    };

    renderActions = () => {
        const { i18n } = this.props;
        const { isSendMessageBtnEnabled } = this.state;

        return (
            <div className={`${this.baseCls}__actions_container`}>
                <Button
                    contextCls={cx(`${this.baseCls}__attach-btn`)}
                    renderIcon={this.renderAttachIcon}
                    onClick={this.handleAttachBtnClick}
                    variant={SECONDARY}
                    aria-label={i18n.getTranslation(
                        'chatWindow.actionsContainer.attachBtnAriaLabel',
                    )}
                />
                <Button
                    contextCls={cx(`${this.baseCls}__send-btn`)}
                    renderIcon={this.renderArrowUpIcon}
                    onClick={this.handleAttachBtnClick}
                    variant={PRIMARY}
                    aria-label={i18n.getTranslation(
                        'chatWindow.actionsContainer.sendBtnAriaLabel',
                    )}
                    isDisabled={!isSendMessageBtnEnabled}
                />
            </div>
        );
    };

    renderAttachIcon = () => {
        return <Attach />;
    };

    renderArrowUpIcon = () => {
        return <EurekaArrowUp />;
    };

    renderAfter = () => {
        const { shouldShowCounter } = this.state;

        return (
            <div className={`${this.baseCls}__after-control-container`}>
                {shouldShowCounter && this.renderCounter()}
                {this.renderActions()}
            </div>
        );
    };

    handleAttachBtnClick = () => {};

    handleResize = (e) => {
        const rows = Math.floor(e.target.scrollHeight / 27);

        if (rows >= 2) {
            this.setState({ shouldShowCounter: true });
        } else {
            this.setState({ shouldShowCounter: false });
        }
    };

    handleMessageChange = (e) => {
        const currentText = e.target.value;
        const characterCount = currentText?.trim().replace(/\n/g, '').length;
        this.setState({ count: characterCount });
        const hasMessage = e.target.value.trim().length > 0;
        this.setState({ isSendMessageBtnEnabled: hasMessage });
    };

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleMinimizeBtnClick = () => {
        // TODO: will replace by minimize the window
        console.log('min btn click');
    };

    baseCls = 'web-chat-window';
}

export default ChatWindow;
